@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-family: sans-serif;
  text-align: center;
  width: 100%;
  max-width: 1224px;
  margin: 0 auto;
}
.generalFont {
  font-family: "Roboto", sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.header {
  display: flex;
  background-color: rgb(0, 213, 177);
  height: 140px;
  width: 100%;
  color: white;
  flex-direction: column;
  align-content: flex-start;
  justify-content: center;
  padding: 24px;
}
.header-title {
  display: flex;
  align-content: center;
  font-size: 24px;
  font-weight: 400;
}
.header-date {
  display: flex;
  align-content: center;
  font-size: 12px;
  font-weight: 150;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.filter {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  background-color: rgb(112, 215, 233);
  height: auto;
  flex-wrap: wrap;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 5px;
  padding-top: 5px;
  border-radius: 3px;
}

.filter-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 5px;
  border: 1px solid #dadada;
  height: 30px;
  background-color: white;
  margin: 1px;
  width: 200px;
}
.filter-input-date {
  display: flex;
  height: 100%;
  border: none;
}
.select-label {
  -webkit-appearance: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 5px;
  border: 1px solid #dadada;
  height: 30px;
  background-color: white;
  margin: 1px;
  width: 200px;
}
.filter-select {
  -webkit-appearance: none;
  display: flex;
  height: 100%;
  border: none;
  width: 80%;
}
.filter-select hover {
  -webkit-appearance: none;
}
.colorSVG {
  fill: red;
}
.imgDolar {
  padding-left: 6px;
}
.buttonReset {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px 0 5px;
  border: 1px solid #dadada;
  height: 30px;
  background-color: white;
  margin: 1px;
  width: auto;
}
.buttonReset:hover {
  background-color: rgb(0, 213, 177);
}
.buttonReset:active {
  background-color: rgb(0, 213, 177);

  -webkit-transform: scale(1.1);

          transform: scale(1.1);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.hotelList {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
  padding: 24px;

  background-image: url("https://images.unsplash.com/photo-1607020914336-15ed7435a306?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80");
  background-size: 100%;
  background-attachment: fixed;
  background-position: center center;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.card {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 300px;
  height: 100%;
  border: 3px solid #ececec;
  margin: 10px;
  background: rgba(255, 255, 255, 0.734);
}
.name {
  display: flex;
  padding: 10px 0 0 10px;
  text-align: left;
  font-size: 24px;
}
.description {
  display: flex;
  padding: 10px 0 0 10px;
  text-align: left;
  font-size: 15px;
}
.boxLocateAndRooms {
  display: flex;
  border-radius: 3px;
  height: 30px;
  box-sizing: content-box;
  margin: 10px 0 0 10px;
  text-align: left;
  font-size: 15px;
  align-items: center;
}
.boxText {
  display: flex;
  align-items: center;
  height: 100%;
  background-color: #f4f4f4;
  padding: 0 10px 0 10px;
  border-radius: 0 4px 4px 0;
}
.boxIcon {
  display: flex;
  align-items: center;
  height: 100%;
  background-color: #249bed;
  padding: 0 0 0 10px;
  border-radius: 4px 0 0 4px;
}
.price {
  display: flex;
  border-radius: 3px;
  height: 30px;
  box-sizing: content-box;
  padding: 0 10px 0 10px;
  text-align: left;
  font-size: 15px;
  align-items: center;
  background-color: #249bed;
  margin: 10px 0 15px 20px;
}
.svgCardLocate {
  margin: 0 10px 0 0;
}
.card-button {
  display: flex;
  background-color: #34d5b1;
  height: 40px;
  width: 100%;
  border: none;
  color: white;
  font-size: 14px;
  font-weight: lighter;
  justify-content: center;
  align-items: center;
}
.whiteDolars {
  color: white;
  font-weight: 600;
  letter-spacing: 2px;
}
.greyDolars {
  color: #57b5f2;
  font-weight: 600;
  letter-spacing: 2px;
}
.card-button:hover {
  background-color: rgb(0, 213, 177);
}
.card-button:active {
  background-color: rgb(0, 213, 177);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
footer {
  display: flex;
  background-color: rgb(0, 213, 177);
  height: 50px;
  width: 100%;
  color: white;
  flex-direction: row;
  align-content: center;
  justify-content: center;
}
.texto-footer {
  display: flex;
  align-items: center;
  flex-direction: center;

  font-size: 14px;
  text-align: center;
}

